@font-face {
  font-family: PTSan;
  src: url("./font/PTSan.ttf");
}
body {
  margin: 0;
  padding: 0;
  font-family: PTSans;
  font-size: 14px;
  font-weight: 300;
  letter-spacing: 0;
  font-style: normal;
  line-height: 1.69;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}
